@import url(https://fonts.googleapis.com/css?family=Nunito+Sans:300,400,600,800);
body{font-size:16px;color:white;background-color:#282c34}a{color:#61dafb;outline:none}h1{font-size:2.25em;font-weight:800;font-style:normal;text-align:center;letter-spacing:-1px}h1:first-child{margin-top:0}

.App{font-size:1em;font-family:'Nunito Sans', helvetica, arial, sans-serif;font-weight:600;text-align:center;margin:0em auto 2em;max-width:48em;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}header{border-bottom:1px solid rgba(255,255,255,0.1);margin-bottom:1em}.content{font-size:1.15em;line-height:1.5em;padding:1.5em 1.75em 0}article{text-align:left}@media screen and (max-width: 520px){.App{font-size:0.85em}}

.navigation{margin:2em auto 2.5em;padding:0 1.25em;display:inline-block;vertical-align:middle;background:transparent;white-space:nowrap;border:3px solid white;border-radius:2.5em}.navigation a{color:white;font-size:1.1em;font-weight:800;text-decoration:none;cursor:pointer;padding:0.575em 0.65em;display:inline-block}.navigation a.active{color:#61dafb}

.search{width:100%;display:block;padding-top:0.5em;padding-bottom:0.5em;padding-left:2.95em;padding-right:0.75em;color:#333;font-size:1.5em;font-weight:700;border:none;border-radius:0.5em;background:white;box-sizing:border-box;outline:none;-webkit-appearance:none;-moz-appearance:none;appearance:none;background-repeat:no-repeat;background-position-y:50%;background-position-x:1em;background-color:white;background-size:1em}@media screen and (max-width: 520px){.search{font-size:1.15em}}

.movie{color:#262626;text-align:left;max-width:13.63em;margin:0 auto 2em;position:relative;box-sizing:border-box;background-color:#525763}.movie:active,.movie:hover{background-color:#696f7d;cursor:pointer;outline:2px solid white}.movie .inside{padding:1.35em 1.75em 1.75em}.movie a{text-decoration:none}.movie h2{font-size:inherit;line-height:1.5em;margin:0;text-align:center;color:#101010}.movie .imgMovie{display:flex;align-items:center;flex-direction:row;justify-content:space-between;margin-top:1.5em}.movie img{width:10em;border:1px solid #000000;border-radius:0.3em;min-height:14em}.movie audio::-webkit-media-controls-panel,.movie audio::-webkit-media-controls{background:#fff}.movie .meta{font-size:0.75em;font-weight:800;line-height:1.5;color:rgba(255,255,255,0.9);padding:1.5em 2.25em;background:#101010}@media screen and (max-width: 520px){.player{flex-direction:column}}.song-enter-active,.song-exit-active{transition:all 0.2s}.song-enter{opacity:0;transform:scale(0.85)}.song-enter-active{opacity:1;transform:translateX(0)}.song-exit{opacity:1}.song-exit-active{opacity:0;transform:scale(0.85)}

.moviesList{margin:3em 0 0;padding-left:0;list-style:none}.moviesList li{margin-bottom:3em}.moviesList li:last-child article{margin-bottom:0}

.detailMovie{color:#262626;text-align:left;max-width:100%;margin:0 auto 2em;position:relative;box-sizing:border-box;background-color:#525763}.detailMovie .divstar{height:0}.detailMovie .star{cursor:pointer;font-size:2em;position:relative;right:0.5em;text-align:center;color:#33c333;padding:0 0.24em}.detailMovie .starActive{font-size:2.3em;color:#1dff1d}.detailMovie .detailInside{padding:1.35em 1.75em 1.75em}.detailMovie .detailDivPicture{float:left;width:10em}.detailMovie .detailText{float:left;max-width:60%;padding-left:1em;font-size:0.85em}.detailMovie .plot{background:#525763;border:1px solid #282c34;padding:0.7em;border-radius:0.3em;font-size:0.9em;margin-top:20em}.detailMovie .plot{clear:both}.detailMovie p{margin:0}.detailMovie a{text-decoration:none}.detailMovie h2{font-size:inherit;line-height:1.5em;margin:0;text-align:center;color:#101010;margin-bottom:1em}.detailMovie img{width:10em;border-radius:0.3em;min-height:14em}.detailMovie audio::-webkit-media-controls-panel,.detailMovie audio::-webkit-media-controls{background:#fff}.detailMovie .meta{font-size:0.75em;font-weight:800;line-height:1.5;color:rgba(255,255,255,0.9);padding:1.5em 2.25em;background:#101010}@media screen and (max-width: 520px){.player{flex-direction:column}}.song-enter-active,.song-exit-active{transition:all 0.2s}.song-enter{opacity:0;transform:scale(0.85)}.song-enter-active{opacity:1;transform:translateX(0)}.song-exit{opacity:1}.song-exit-active{opacity:0;transform:scale(0.85)}

.moviesList{margin:3em 0 0;padding-left:0;list-style:none}.moviesList li{margin-bottom:3em}.moviesList li:last-child article{margin-bottom:0}.btn{background:#999fad;padding:0.6em 0.9em;border-radius:0.3em;text-transform:uppercase;color:black;font-size:0.9em;border:2px solid #0e0e0e;margin:0.4em;cursor:pointer}.btnDetail{margin-left:2.5em}.btnDetail:hover,.btnDetail:active{background-color:#7787ab}.btnRemove{background:#b78383}.btnRemove:hover,.btnRemove:active{background:#c16767}.movieFavorite{color:#262626;text-align:left;max-width:28.0em;margin:0 auto 2em;position:relative;box-sizing:border-box;background-color:#525763;border:3px solid #181b1f}.movieFavorite .inside{padding:1.35em 1.75em 1.75em}.movieFavorite a{text-decoration:none}.movieFavorite h2{font-size:inherit;line-height:1.9em;margin:0;text-align:center;color:#101010;border-bottom:1px solid #4b4f58;padding-bottom:0.5em}.movieFavorite .imgMovie{display:flex;align-items:center;flex-direction:row;margin-top:1.5em}.movieFavorite img{width:10em;border:1px solid #000000;border-radius:0.3em;min-height:14em}.movieFavorite audio::-webkit-media-controls-panel,.movieFavorite audio::-webkit-media-controls{background:#fff}.movieFavorite .meta{font-size:0.75em;font-weight:800;line-height:1.5;color:rgba(255,255,255,0.9);padding:1.5em 2.25em;background:#101010}@media screen and (max-width: 520px){.player{flex-direction:column}}.song-enter-active,.song-exit-active{transition:all 0.2s}.song-enter{opacity:0;transform:scale(0.85)}.song-enter-active{opacity:1;transform:translateX(0)}.song-exit{opacity:1}.song-exit-active{opacity:0;transform:scale(0.85)}

